@charset "UTF-8";

@import "common/_base";
@import "common/_mixins";
@import "common/_preloader.scss"; // Loading Circle
@import "web_font"; // 웹폰트
@import "growthy-font-map"; //아이콘폰트

#wrap {
    position: relative;
    min-width: 1360px;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.tooltip{
    .tooltip_item{
        position: fixed;
        display: block;
        font-size: 12px;
        line-height: 18px;
        color: #fff;
        background-color: #212121;
        opacity: 0.8;
        filter: alpha(opacity=80);
        padding: 12px;
        border-radius: 4px;
        -webkit-border-radius: 4px;
        z-index: 90;
    }
    .grid_cell_info{
        border: 1px solid #00a000;
        padding: 11px 11px;
        position: fixed;
        display: block;
        font-size: 12px;
        line-height: 18px;
        background-color: #fff;
        .inner_cell_info{
            position: relative;
            border-collapse: collapse;
            border-spacing: 0;
            empty-cells: show;
            table-layout: fixed;
            tr{
                &:first-child{
                    td{
                        padding-bottom: 7px;
                    }
                }
                td{
                    height: 22px;
                    vertical-align: middle;
                    font-size: 12px;
                    line-height: 20px;
                    color: #757575;
                    min-width: 70px;
                    &:first-child{
                        font-family: robotoMedium;
                        font-size: 11px;
                        color: #212121;
                    }
                }
            }
            &:before{
                content: '';
                position: absolute;
                left: 0;
                top: 22px;
                width: 100%;
                height: 1px;
                background-color: #f0f0f0;
            }
        }
    }
}
.container{
    overflow: hidden;
    position: relative;
    .inner_container{
        background-color: #fafafa;
    }
    input[type="text"]::-webkit-input-placeholder{
        color:#ccc;
        display: inline-block;
        width: 100%;
    }
    input[type="text"]:-ms-input-placeholder{
        color:#ccc;
    }
    input[type="text"]:-moz-input-placeholder{
        color:#ccc;
    }
    input[type=text]::-ms-clear {
        display: none;
        width: 0;
        height: 0;
    }
    input[type=text]::-ms-reveal {
        display: none;
        width: 0;
        height: 0;
    }
    &.full_screen_mode{
        .content{
            margin: 0;
            .inner_content_section{
                padding: 30px;
            }
        }
    }
}
.include_header {
    position: relative;
    z-index: 50;
    .anno_banner_section{
        height: 40px;
        background-color: #e2f1ff;
        text-align: center;
        line-height: 40px;
        .banner_info{
            display: inline-block;
            font-size: 12px;
            @include character-reduce;
            max-width: 1200px;
        }
        .btn_more{
            display: inline;
            font-size: 12px;
            text-decoration: underline;
            color: #007dff;
            margin-left: 30px;
            line-height: 40px;
            vertical-align: top;
        }
    }
    .header_section{
        position: relative;
        height: 64px;
        background-color: $primary-color-green;
        @include box_shadow(3);
        .sevice_logo{
            display: block;
            position: absolute;
            width: 202px;
            height: 22px;
            top: 21px;
            left: 20px;
            .brand_logo{
                display: inline-block;
                width: 202px;
                height: 22px;
                background-image: url("~@/assets/img/img_brand_logo.png");
                background-repeat: no-repeat;
            }
        }
        .more_menu{
            position: absolute;
            top: 0;
            right: 0;
            height: 64px;
            .guide_more{
                display: inline-block;
                position: relative;
                height: 100%;
                line-height: 64px;
                vertical-align: middle;
                &:after{
                    @include float-clear;
                }
                .guide_more_inner{
                    float: left;
                    li{
                        float: left;
                        position: relative;
                        .btn_menu_item{
                            position: relative;
                            margin: 0 10px;
                            color: #fff;
                            font-size: 12px;
                            &:before{
                                content: '';
                                position: absolute;
                                top: 3px;
                                left: -10px;
                                width: 1px;
                                height: 8px;
                                background-color: #fff;
                            }
                        }
                        &:first-child{
                            .btn_menu_item{
                                &:before{
                                    display: none;
                                }
                            }
                        }
                        
                    }
                    &:after{
                        @include float-clear;
                    }
                    .guide_detail_menu{
                        top: 71px;
                        
                    }
                }
                .ico_guide_more{
                    float: left;
                    display: inline-block;
                    position: relative;
                    width: 20px;
                    height: 20px;
                    margin: 22px 0 0 10px;
                    .btn_ico_guide{
                        &:before{
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 20px;
                            height: 20px;
                            line-height: 20px;
                            color: #fff;
                            @include bg_icon_font('\E01D', 20px)
                        }
                    }
                    
                }
                .guide_detail_menu{
                    position: absolute;
                    top: 49px;
                    right: 0;
                    width: 200px;
                    z-index: 61;
                    @include box_shadow(3);
                    background-color: $primary-color-white;
                    padding: 10px 0;
                    ul{
                        li{
                            width: 100%;
                            font-size: 13px;
                            display: block;
                            height: 32px;
                            a{
                                display: block;
                                height: 32px;
                                width: 100%;
                                color: #757575;
                                span{
                                    display: block;
                                    padding: 0 20px;
                                    line-height: 32px;
                                    text-overflow: ellipsis;
                                    overflow: hidden;
                                    word-break: break-all;
                                    white-space: nowrap;
                                    i{
                                        vertical-align: middle;
                                        margin-left: 5px;
                                        color: #bdbdbd;
                                    }
                                }
                                &:hover{
                                    background-color: #e6f5e4;
                                }
                            }
                        }
                    }
                }
                
            }
            .service_list{
                display: inline-block;
                position: relative;
                margin-left: 20px;
                .btn_service{
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    background-image: url("~@/assets/img/ic-gnb-menu.png");
                    background-repeat: no-repeat;
                    vertical-align: middle;
                    line-height: 64px;
                }
                .service_category{
                    position: absolute;
                    top: 49px;
                    right: 0;
                    width: 200px;
                    z-index: 60;
                    @include box_shadow(3);
                    background-color: $primary-color-white;
                    ul{
                        border-top: 1px solid #f0f0f0;
                        padding: 10px 0;
                        &:first-child{
                            border-top: 0;
                        }
                        li{
                            display: block;
                            height: 32px;
                            a{
                                display: block;
                                height: 32px;
                                width: 100%;
                                &.btn{
                                    background-image: url("~@/assets/img/spr_gnb_service_logo.png");
                                    background-repeat: no-repeat;
                                    &.s_growthy{
                                        background-position: 20px 8px;
                                    }
                                    &.s_map{
                                        background-position: 20px -28px;
                                    }
                                    &.s_ico_text{
                                        background-position: 20px -64px;
                                        @include character-reduce;
                                        padding: 0 20px 0 40px;
                                        span{
                                            line-height: 32px;
                                            font-size: 13px;
                                            color: #212121;
                                        }
                                    }
                                    &:hover{
                                        background-color: #e6f5e4
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .account_login{
                position: relative;
                display: inline-block;
                vertical-align: middle;
                height: 100%;
                margin-left: 29px;
                .btn_account_area{
                    display: inline-block;
                    line-height: 64px;
                    padding-left: 30px;
                    padding-right: 20px;
                    &:hover,&.selected{
                        background-color: #16af0f;
                    }
                    i{
                        font-size: 16px;
                    }
                }
                .account_info{
                    font-size: 12px;
                    vertical-align: top;
                    color: $primary-color-white;
                }
                i.fn-map{
                    vertical-align: middle;
                    color: $primary-color-white;
                    margin-left: 4px;
                }
                .account_detail_info{
                    position: absolute;
                    top: 71px;
                    right: 24px;
                    width: 240px;
                    z-index: 61;
                    @include box_shadow(3);
                    background-color: $primary-color-white;
                    .more_detail_info{
                        padding: 20px;
                        border-bottom: 1px solid #f0f0f0;
                        .account_nickname{
                            display: block;
                            font-size: 16px;
                            line-height: 20px;
                            color: $primary-color-black;
                            margin-bottom: 5px;
                            @include character-reduce
                        }
                        .account_email{
                            display: block;
                            font-size: 14px;
                            line-height: 20px;
                            color: #bdbdbd;
                            word-break: break-all;
                        }
                    }
                    ul{
                        &.interval{
                            padding: 10px 0;
                        }
                        &.sign_out{
                            border-top: 1px solid #f0f0f0;
                        }
                        li{
                            position: relative;
                            font-size: 13px;
                            display: block;
                            height: 32px;
                            .switch_menu,
                            a{
                                display: block;
                                position: relative;
                                height: 32px;
                                width: 100%;
                                color: #757575;
                                span{
                                    display: block;
                                    padding: 0 20px;
                                    line-height: 32px;
                                    @include character-reduce;
                                }
                                &:hover,
                                &.on{
                                    background-color: #e6f5e4;
                                    color: #00a000;
                                }
                            }
                            .switch_menu{
                                &:hover{
                                    background-color: transparent
                                }
                            }
                            .report_save{
                                display: inline-block;
                                position: absolute;
                                top: 9px;
                                right: 20px;
                            }
                            .btn_set_more{
                                &:before{
                                    position: absolute;
                                    top: 9px;
                                    right: 20px;
                                    @include bg_icon_font('\E00C', 16px)
                                    color: #212121;
                                }
                            }
                            .set_more_menu{
                                position: absolute;
                                top: 32px;
                                right: 20px;
                                width: 120px;
                                z-index: 71;
                                @include box_shadow(3);
                                background-color: $primary-color-white;
                                padding: 10px 0;
                                ul{
                                    li{
                                        width: 100%;
                                        font-size: 13px;
                                        display: block;
                                        height: 28px;
                                        a{
                                            position: absolute;
                                            display: block;
                                            height: 28px;
                                            width: 100%;
                                            color: #757575;
                                            span{
                                                padding: 0 20px 0 15px;
                                                line-height: 28px;
                                                i{
                                                    vertical-align: middle;
                                                    margin-left: 5px;
                                                    color: #bdbdbd;
                                                }
                                            }
                                            &:hover{
                                                background-color: #e6f5e4;
                                            }
                                            &.on{
                                                position: relative;
                                                background-color: #e6f5e4;
                                                color: #00a000;
                                                &:before{
                                                    position: absolute;
                                                    top: 6px;
                                                    right: 20px;
                                                    @include bg_icon_font('\E014', 16px);
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
.include_nav {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 260px;
    height: 100%;
    @include lnb_transition;
    background-color: $primary-color-white;
    z-index: 88;
    @include box_shadow(2);
    .nav_section{
        height: 100%;
        position: relative;
        .nav_top{
            position: relative;
            height: 84px;
            border-bottom: 1px solid #dbdbdb;
            transition: height 0.5s ease;
            -webkit-transition: height 0.5s ease;
            box-shadow: inset 0 9px 11px -9px rgba(0,0,0,0.6);
            -webkit-box-shadow: inset 0 9px 11px -9px rgba(0,0,0,0.6);
            .project_img{
                position: absolute;
                top:20px;
                left: 20px;
                transition: all 0.5s ease;
                .img_area{
                    position: relative;
                    width: 44px;
                    height: 44px;
                    background: url(~@/assets/img/bg_project_no_img.png) 0 0 no-repeat;
                    background-size: cover;
                    -webkit-background-size: cover;
                    transition: all 0.5s ease;
                    -webkit-transition: all 0.5s ease;
                    .img_mask{
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 46px;
                        height: 46px;
                        margin: -1px 0 0 -1px;
                        background: url(~@/assets/img/bg_project_img_mask_resize.png) 0 0 no-repeat;
                        background-size: cover;
                        -webkit-background-size: cover;
                        transition: all 0.5s ease;
                        -webkit-transition: all 0.5s ease;
                        z-index: 1;
                    }
                }
            }
            .project_name{
                display: inline-block;
                padding: 26px 0 0 76px;
                @include lnb_transition_show;
                .btn_project_list{
                    position: relative;
                    display: block;
                    width: 164px;
                    height: 32px;
                    line-height: 16px;
                    padding-right: 32px;
                    font-size: 14px;
                    color: #212121;
                    overflow: hidden;
                    &:before{
                        position: absolute;
                        top: 6px;
                        right: 0;
                        @include bg_icon_font('\E005', 20px);
                    }
                    span{
                        display: table-cell;
                        width: 132px;
                        height: 32px;
                        vertical-align: middle;
                        
                    }
                }
            }
            .layer_project_search{
                position: absolute;
                top: 76px;
                left: 20px;
                width: 400px;
                height: auto;
                background-color: #fff;
                @include box_shadow(3);
                z-index: 90;
                .search_area{
                    .inner_search_area{
                        position: relative;
                        padding: 10px 20px 9px;
                        border-bottom: 1px solid #bdbdbd;
                        button{
                            display: inline-block;
                            .ico_secrch{
                                position: relative;
                                display: inline-block;
                                width: 18px;
                                height: 18px;
                                vertical-align: middle;
                                &:before{
                                    position: absolute;
                                    top: 0;
                                    right: 0;
                                    width: 18px;
                                    height: 18px;
                                    line-height: 18px;
                                    color: #9e9e9e;
                                    @include bg_icon_font('\E035', 18px);
                                }
                            }
                        }
                        input{
                            border: 0;
                            padding: 0;
                            background-color: transparent;
                            height: 32px;
                            color: #424242;
                            width: 88%;
                            text-align: left;
                            -webkit-appearance: none;
                        }
                        .clear_input{
                            display: none;
                            position: absolute;
                            top: 18px;
                            right: 20px;
                            width: 16px;
                            height: 16px;
                            &:before{
                                @include bg_icon_font('\E031', 16px);
                                color: #bdbdbd;
                            }
                        }
                        input[type="text"]:focus,
                        input[type="text"]:active,
                        input[type="text"]:visited{
                            & + a.clear_input{
                                display: block;
                            }
                        }
                    }
                }
                .result_area{
                    overflow-y: hidden;
                    .inner_result_area{
                        overflow-y: auto;
                        margin: 10px 0;
                        ul{
                            &.bookmark_list{
                                border-bottom: 1px solid #f0f0f0;
                            }
                        }
                        li{
                            &:hover{
                                background-color: #f2faf1
                            }
                            &.selected{
                                .name_area{
                                    color: #00a000;
                                }
                            }
                        }
                        .project_list_item{
                            position: relative;
                            padding: 10px 20px;
                            .img_area{
                                float: left;
                                position: relative;
                                width: 44px;
                                height: 44px;
                                background: url(~@/assets/img/bg_project_no_img.png) 0 0 no-repeat;
                                .img_mask{
                                    position: absolute;
                                    top: 0;
                                    left: 0;
                                    width: 46px;
                                    height: 46px;
                                    margin: -1px 0 0 -1px;
                                    background: url(~@/assets/img/bg_project_img_mask_resize.png) 0 0 no-repeat;
                                    z-index: 1;
                                }
                                
                            }
                            .name_area{
                                overflow: hidden;
                                float: left;
                                width: 214px;
                                height: 40px;
                                margin: 2px 0 0 12px;
                                span{
                                    display: table-cell;
                                    vertical-align: middle;
                                    height: 40px;
                                    overflow: hidden;
                                    line-height: 20px;
                                    em{
                                        color: #00a000;
                                    }
                                }
                                &:before{
                                    content: '';
                                    display: table;
                                }
                            }
                            &:after{
                                @include float-clear;
                            }
                            .check_bookmark{
                                position: absolute;
                                top: 23px;
                                right: 78px;
                                width: 16px;
                                height: 16px;
                                &:before{
                                    @include bg_icon_font('\E05B', 16px);
                                    color: #e0e0e0;
                                }
                                &.on{
                                    &:before{
                                        color: #00a000;
                                    }
                                }
                            }
                            .check_sign{
                                display: none;
                                position: absolute;
                                top: 23px;
                                right: 50px;
                                width: 16px;
                                height: 16px;
                                &:before{
                                    @include bg_icon_font('\E014', 16px);
                                    color: #00a000;
                                }
                                &.on{
                                    display: block
                                }
                            }
                            .check_setting{
                                position: absolute;
                                top: 23px;
                                right: 20px;
                                width: 18px;
                                height: 18px;
                                &:before{
                                    @include bg_icon_font('\E036', 18px);
                                    color: #9e9e9e
                                }
                            }
                        }
                        
                    }
                    .result_nothing{
                        font-size: 14px;
                        line-height: 20px;
                        color: #bdbdbd;
                        text-align: center;
                        padding: 40px 0;
                    }
                }
            }
        }
        .inner_nav_section{
            position: relative;
            height: calc(100% - 96px - 84px);
            overflow-y: auto;
            @include lnb_transition_show;
            ul{
                &.depth_menu{
                    li{
                        a{
                            display: block;
                            word-break: break-all;
                            &:hover{
                                background-color: rgba(230, 245, 228, 0.5);
                            }
                            &.paid{
                                position: relative;
                                &:before{
                                    position: absolute;
                                    top: 16px;
                                    right: 20px;
                                    @include bg_icon_font('\E055', 16px);
                                    color: #bdbdbd;
                                }
                            }
                        }
                    }
                    &.one{
                        @include lnb_transition_show;
                        & > li{
                            border-bottom: 1px solid #f0f0f0;
                            a{
                                padding: 16px 20px 15px 40px;
                                font-size: 14px;
                                color: $primary-color-black;
                            }
                            &.depth_group{
                                & > a{
                                    span{
                                        position: relative;
                                        &:before{
                                            content: '\E00C';
                                            position: absolute;
                                            top: 0;
                                            left: -20px;
                                            font-family: growthy-map-icons;
                                            font-size: 16px;
                                            color: #bdbdbd;
                                        }
                                        em{
                                            font-size: 10px;
                                            color: #e74b80;
                                            margin-left: 5px;
                                            font-family: robotoMedium;
                                        }
                                    }
                                }
                                &.open{
                                    & > a{
                                        span{
                                            position: relative;
                                            &:before{
                                                content: '\E00A';
                                                position: absolute;
                                                top: 0;
                                                left: -20px;
                                                font-family: growthy-map-icons;
                                                font-size: 16px;
                                                color: #bdbdbd;
                                            }
                                        }
                                    }
                                    .depth_menu.two{
                                        display: block;
                                        padding-bottom: 19px;
                                        .menu_group_title{
                                            display: block;
                                            height: 20px;
                                            line-height: 20px;
                                            font-size: 11px;
                                            color: #bdbdbd;
                                            padding-left: 48px;
                                            margin-top: 12px;
                                        }
                                        li{
                                            &:first-child{
                                                .menu_group_title{
                                                    margin-top: 0;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            &.selected{
                                a{
                                    background-color: rgba(230, 245, 228, 0.5);
                                    color: $primary-color-green;
                                }
                            }
                        }
                    }
                    &.two{
                        display: none;
                        & > li{
                            a{
                                padding: 9px 20px 8px 56px;
                                font-size: 13px;
                                color: #757575;
                            }
                            .group_menu{
                                li{
                                    a{
                                        span{
                                            em{
                                                font-size: 10px;
                                                color: #e74b80;
                                                margin-left: 5px;
                                                font-family: robotoMedium;
                                            }
                                        }
                                        &.paid{
                                            &:before{
                                                top: 9px;
                                            }
                                        }
                                    }
                                    &.selected{
                                        a{
                                            background-color: rgba(230, 245, 228, 0.5);
                                            color: $primary-color-green;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .inner_close_nav_section{
            @include lnb_transition_hide;
            height: 0;
        }
        .nav_bottom{
            position: absolute;
            left: 0;
            bottom: 1px;
            width: 100%;
            background-color: #fff;
            li{
                border-top: 1px solid #f0f0f0;
                height: 48px;
                .btn_nav{
                    position: relative;
                    display: inline-block;
                    width: 100%;
                    font-size: 12px;
                    color: #212121;
                    padding: 17px 20px 17px 45px;
                    &.all_menu{
                        &:before{
                            position: absolute;
                            top: 13px;
                            left: 19px;
                            @include bg_icon_font('\E043', 20px);
                        }
                        span{
                            @include lnb_transition_show;
                        }
                    }
                    &.nav_action{
                        position: relative;
                        padding: 0;
                        height: 48px;
                        .btn_move_nav{
                            &:before{
                                position: absolute;
                                top: 13px;
                                left : 19px;
                                @include bg_icon_font('\E01A', 20px);
                                color: #bdbdbd;
                                z-index: 10;
                            }
                            &:hover{
                                &:after{
                                    content: '';
                                    position: absolute;
                                    top: 5px;
                                    left : 10px;
                                    width: 36px;
                                    height: 36px;
                                    background-color: #f5f5f5;
