            transition: height 0.5s ease;
            -webkit-transition: height 0.5s ease;
            .project_name{
                @include lnb_transition_hide;
            }
            .project_img{
                top: 12px;
                left: 12px;
                transition: all 0.5s ease;
                .img_area{
                    width: 36px;
                    height: 36px;
                    background-size: cover;
                    -webkit-background-size: cover;
                    transition: all 0.5s ease;
                    -webkit-transition: all 0.5s ease;
                    .img_mask{
                        width: 38px;
                        height: 38px;
                        margin: -1px 0 0 -1px;
                        background-size: cover;
                        -webkit-background-size: cover;
                        transition: all 0.5s ease;
                        -webkit-transition: all 0.5s ease;
                    }
                }
            }
        }
        .inner_nav_section{
            height: 0;
            @include lnb_transition_hide;
        }
        .nav_bottom{
            li{
                .btn_nav{
                    &.all_menu{
                        span{
                            @include lnb_transition_hide;
                        }
                    }
                    &.nav_action{
                        .btn_move_nav{
                            &:before{
                                @include bg_icon_font('\E021', 20px);
                            }
                        }
                    }
                }
            }
        }
        .inner_close_nav_section{
            position: absolute;
            top: 0;
            left: 0;
            width: 60px;
            height: calc(100% - 96px);
            z-index: 85;
            overflow: hidden;
            @include lnb_transition_show;
            .hidden_item{
                position: relative;
                height: 48px;
                border-bottom: 1px solid #f0f0f0;
                .ico_hidden_menu{
                    &:before{
                        position: absolute;
                        top: 14px;
                        left: 20px;
                        @include bg_icon_font('\E056', 20px);
                    }
                }
            }
            ul{
                padding-top: 60px;
            }
        }
        & + div.content{
            margin-left: 60px;
            @include lnb_transition_content;
        }
        .all_menu_section{
            transition: all 1s ease;
            transition-delay: 0.2s;
            -webkit-transition: all 1s ease;
            -webkit-transition-delay: 0.2s;
            left: 90px;
        }
        // LNB 닫힌 상태에서 마우스 오버
        &.over{
            width: 260px;
            @include lnb_transition;
            .nav_top{
                height: 84px;
                transition: height 0.5s ease;
                -webkit-transition: height 0.5s ease;
                .project_name{
                    @include lnb_transition_show;
                }
                .project_img{
                    top: 20px;
                    left: 20px;
                    transition: all 0.5s ease;
                    .img_area{
                        width: 44px;
                        height: 44px;
                        background-size: cover;
                        -webkit-background-size: cover;
                        transition: all 0.5s ease;
                        -webkit-transition: all 0.5s ease;
                        .img_mask{
                            width: 46px;
                            height: 46px;
                            background-size: cover;
                            -webkit-background-size: cover;
                            transition: all 0.5s ease;
                            -webkit-transition: all 0.5s ease;
                        }
                    }
                }
            }
            .inner_nav_section{
                height: calc(100% - 96px - 84px);
                @include lnb_transition_show;
            }
            .nav_bottom{
                li{
                    .btn_nav{
                        &.all_menu{
                            span{
                                @include lnb_transition_show;
                            }
                        }
                    }
                }
            }
            .inner_close_nav_section{
                @include lnb_transition_hide;
                height: 0;
            }
            & + div.content{
                margin-left: 60px;
            }
            .all_menu_section{
                transition: all 1s ease;
                transition-delay: 0.2s;
                -webkit-transition: all 1s ease;
                -webkit-transition-delay: 0.2s;
                left: 290px
            }
        }
    }
}
