    }

    .fixedTable-header {
        position: relative;
        width: calc(100% - 469px);
        margin-left: 469px;
        overflow: hidden;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #e0e0e0;
        font-size: 12px;
