                height: 32px;
                border-bottom: 1px solid #f0f0f0;
                td{
                    font-size: 12px;
                    color: #757575;
                    border-right: 1px solid #e0e0e0;
                    &.category{
                        width: 119px;
                    }
                    &.title{
                        width: 120px;
                    }
                    &.month{
                        width: 230px;
                        padding-right: 0;
                    }
                }
            }
        }
    }
    .fixedTable-body {
        position: relative;
        width: calc(100% - 469px);
        height: 480px;
        overflow: auto;
        background-color: #fff;
        tr {
            height: 32px;
            border-bottom: 1px solid #e0e0e0;
            &.m_over{
                background-color: #fcfefb;
                td{
                    &.m_over{
                        background-color: #f2faf1;
                    }
                }
            }
            td {
                font-size: 12px;
                color: #757575;
                text-align: right;
                border-right: 1px solid #e0e0e0;
                position: relative;
                &.m_over{
                    background-color: #fcfefb;
                }
                &.selected{
                    background-color: #f2faf1;
                }
                .up{
                    color: #007dff;
                    &.arrow{
                        position: relative;
                        &:before{
                            position: absolute;
                            top: -1px;
                            left: -16px;
                            @include bg_icon_font('\E006', 16px);
                        }
                    }
                }
                .down{
                    color: #d0021b;
                    &.arrow{
                        position: relative;
                        &:before{
                            position: absolute;
                            top: -1px;
                            left: -16px;
                            @include bg_icon_font('\E005', 16px);
                        }
                    }
                }
                .maintain{
                    color: #212121;
                    &.zero_percent{
                        position: relative;
                        &:before{
                            content: '';
                            position: absolute;
                            top: 6px;
                            left: -11px;
                            width: 6px;
                            height: 2px;
                            background-color: #9e9e9e;
                        }
                    }
                }
            }
        }
    }
    &:after{
        @include float-clear;
    }
    .btn_sort{
        display: block;
        position: relative;
        &:before{
            position: absolute;
            right: 0;
            top: 2px;
            width: 16px;
            height: 16px;
            line-height: 16px;
            @include bg_icon_font('\E005', 16px);
            color: #9e9e9e;
        }
        &:after{
            position: absolute;
            right: 0;
            bottom: 2px;
            width: 16px;
            height: 16px;
            line-height: 16px;
            @include bg_icon_font('\E006', 16px);
            color: #9e9e9e;
        }
    }
    &.add_header_type{
        .fake-header{
            height: 100px;
            table{
                &._fake{
                    tr{
                        &.merged{
                            border-bottom:0;
                            td{
                                height: 66px;
                                vertical-align: middle;
                                padding: 0 10px;
                            }
                            .kpi{
                                border-bottom: 0;
                                border-right: 0;
                            }
                        }
                    }
                }
            }
        }
        .fixedTable-header{
            text-align: left;
            ._header{
                .merged_th{
                    border-bottom: 1px solid #e0e0e0;
                    height: 32px;
                    & + tr{
                        height: 66px;
                    }
                }
            }
        }
    }
