}
// set_layer
.set_layer{
    position: absolute;
    width: 738px;
    background-color: #fff;
    @include box_shadow(4);
    z-index: 70;
    .setting_group{
        .set_first{
            float: left;
            height: 100%;
            width: 264px;
            border-right: 1px solid #f0f0f0;
        }
        .set_second{
            float: left;
            height: 100%;
            position: relative;
            width: 264px;
            border-right: 1px solid #f0f0f0;
            &:before{
                content: '';
                position: absolute;
                top: 50%;
                right: -16px;
                width: 16px;
                height: 30px;
                margin-top: -15px;
                background: url("~@/assets/img/bg_filter_arrow.png") 0 0 no-repeat;
                z-index: 2;
            }
            &:after{
                content: '';
                position: absolute;
                top: 0;
                right: -16px;
                width: 16px;
                height: 100%;
                background: url("~@/assets/img/bg_v_repeat_shadow.png") 0 0 repeat-y;
            }
        }
        .set_third{
            float: left;
            height: 100%;
            width: 210px;
            padding: 20px 0 20px 20px;
            .inner_date_set{
                dl{
                    position: relative;
                    margin-bottom: 8px;
                    dt{
                        display: inline-block;
                        position: absolute;
                        top: 10px;
                        left: 0;
                        font-family: robotoMedium;
                        font-size: 12px;
                        color: #212121;
                    }
                    dd{
                        display: block;
                        margin-left: 37px;
                        width: 132px;
                        .set_date_val{
                            width: 132px;
                            height: 32px;
                            line-height: 32px;
                            font-size: 13px;
                            color: #757575;
                            padding-left: 10px;
                            border: 1px solid #ccc;
                            border-radius: 4px;
                            -webkit-border-radius: 4px;
                            letter-spacing: -0.25px;
                        }
                    }
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
        .obj_dimension_list{
            height: 320px;
            overflow-y: auto;
            margin: 14px 0;
            li{
                display: block;
                width: 100%;
                .dimension_item{
                    position: relative;
                    display: block;
                    width: 100%;
                    height: 32px;
                    font-size: 13px;
                    line-height: 32px;
                    color: #757575;
                    padding: 0 40px 0 20px;
                    @include character-reduce;
                    &.in_filter{
                        &:before{
                            position: absolute;
                            top: 10px;
                            right: 21px;
                            width: 12px;
                            height: 12px;
                            line-height: 12px;
                            @include bg_icon_font('\E03E', 12px);
                            color: #bdbdbd;
                        }
                    }
                    &:hover{
                        background-color: #f2faf1;
                    }
                }
                &.on{
                    background-color: #f2faf1;
                    .dimension_item{
                        color: #00a000;
                    }
                }
            }
        }
        .no_set_data{
            display: table;
            text-align: center;
            width: 100%;
            height: 100%;
            & > span{
                display: table-cell;
                vertical-align: middle;
                color: #bdbdbd;
                font-size: 13px;
            }
        }
        .direct_input{
            height: 100%;
            padding: 22px 20px 20px 20px;
            .title_info{
                position: relative;
                font-size: 13px;
                color: #212121;
                margin-bottom: 22px;
                @include character-reduce;
            }
            .direct_input_add{
                margin-bottom: 12px;
                dt{
                    font-family: robotoMedium;
                    font-size: 12px;
                    color: #212121;
                    margin-bottom: 4px;
                }
                .input_search{
                    .inner_input_search{
                        position: relative;
                        border: 1px solid #ccc;
                        border-radius: 4px;
                        -webkit-border-radius: 4px;
                        button{
                            display: inline-block;
                            .ico_add{
                                position: relative;
                                display: inline-block;
                                width: 18px;
                                height: 18px;
                                vertical-align: middle;
                                &:before{
                                    position: absolute;
                                    top: 0;
                                    right: 0;
                                    width: 18px;
                                    height: 18px;
                                    line-height: 18px;
                                    color: #9e9e9e;
                                    @include bg_icon_font('\E03B', 18px);
                                }
                            }
                        }
                        input{
                            border: 0;
                            padding: 0;
                            background-color: transparent;
                            height: 30px;
                            color: #424242;
                            width: 85%;
                            text-align: left;
                            -webkit-appearance: none;
                            padding-left: 12px;
                            letter-spacing: -0.5px;
                        }
                    }
                }
            }
            .add_item_list{
                overflow-y: auto;
                height: 207px;
                li{
                    display: block;
                    height: 28px;
                    font-size: 12px;
                    color: #757575;
                    line-height: 28px;
                    padding: 0 8px;
                    @include character-reduce;
                    .btn_close_item{
                        display: inline-block;
                        line-height: 28px;
                        vertical-align: top;
                        width: 16px;
                        height: 16px;
                        margin-left: 8px;
                        &:before{
                            vertical-align: top;
                            @include bg_icon_font('\E00D', 16px);
                        }
                    }
                }
            }
        }
        .obj_filter_list{
            height: 100%;
            padding: 22px 20px 20px 20px;
            .title_info{
                position: relative;
                font-size: 13px;
                color: #212121;
                padding-right: 43px;
                @include character-reduce;
                span{
                    position: absolute;
                    top: 2px;
                    right: 0;
                    font-size: 11px;
                    color: #757575;
                    em{
                        color: #e74b80;
                    }
                }
            }
            .input_search{
                margin: 22px 0 12px 0;
                .inner_input_search{
                    position: relative;
                    border-bottom: 1px solid #ccc;
                    button{
                        display: inline-block;
                        .ico_secrch{
                            position: relative;
                            display: inline-block;
                            width: 18px;
                            height: 18px;
                            vertical-align: middle;
                            &:before{
                                position: absolute;
                                top: 0;
                                right: 0;
                                width: 18px;
                                height: 18px;
                                line-height: 18px;
                                color: #9e9e9e;
                                @include bg_icon_font('\E035', 18px);
                            }
                        }
                    }
                    input{
                        border: 0;
                        padding: 0;
                        background-color: transparent;
                        height: 32px;
                        color: #424242;
                        width: 90%;
                        text-align: left;
                        -webkit-appearance: none;
                    }
                }
            }
            .search_result_list{
                overflow-y: auto;
                height: 224px;
                li{
                    display: block;
                    width: 100%;
                    .result_select_list{
                        position: relative;
                        display: block;
                        width: 100%;
                        height: 28px;
                        font-size: 12px;
                        line-height: 28px;
                        color: #757575;
                        padding-left: 28px;
                        @include character-reduce;
                        &:before{
                            position: absolute;
                            top: 0;
                            left: 8px;
                            @include bg_icon_font('\E008', 16px);
                            color: #757575;
                        }
                        &:hover{
                            background-color: #f2faf1;
                        }
                    }
                    &.on{
                        .result_select_list{
                            color: #00a000;
                            background-color: #f2faf1;
                            &:before{
                                @include bg_icon_font('\E009', 16px);
                                color: #00a000;
                            }
                        }
                    }
                }
            }
        }
        .obj_selected_filter{
            .title_info{
                position: relative;
                font-family: robotoBold, NanumGothicBold;
                font-size: 16px;
                color: #212121;
                padding-right: 80px;
                @include character-reduce;
                span{
                    position: absolute;
                    top: 3px;
                    right: 20px;
                    font-size: 11px;
                    color: #757575;
                    font-family: roboto, NanumGothic;
                    em{
                        color: #e74b80;
                        margin-left: 3px;
                    }
                }
            }
            .selected_filter_list{
                height: 268px;
                padding-right: 20px;
                margin: 20px 0;
                overflow-y: auto;
                li{
                    margin-top: 8px;
                    &:first-child{
                        margin-top: 0;
                    }
                }
                .selected_filter_item{
                    position: relative;
                    padding: 12px 12px 11px;
                    background-color: #f5f5f5;
                    border-radius: 4px;
                    -webkit-border-radius: 4px;
                    .selected_filter_name{
                        font-size: 13px;
                        color: #212121;
                    }
                    .selected_filter_info{
                        font-size: 12px;
                        line-height: 16px;
                        color: #757575;
                        margin-top: 4px;
                        word-break: keep-all;
                    }
                    .btn_delete_item{
                        position: absolute;
                        top: 12px;
                        right: 12px;
                        width: 16px;
                        height: 16px;
                        &:before{
                            @include bg_icon_font('\E031', 16px);
                            color: #bdbdbd;
                        }
                    }
                }
            }
            .no_set_data{
                height: 269px;
                margin-left: -10px;
            }
        }
        &:after{
            @include float-clear;
        }
    }
    .btn_group{
        padding: 12px 20px;
        border-top: 1px solid #bdbdbd;
        overflow: hidden;
        .btn_clear{
            display: inline-block;
            vertical-align: top;
