            float: right;
            .btn{
                display: inline-block;
                vertical-align: top;
                height: 28px;
                width: 80px;
                line-height: 28px;
                font-size: 11px;
                color: #fff;
                text-align: center;
                font-family: robotoMedium;
                border-radius: 4px;
                -webkit-border-radius: 4px;
                @include box_shadow(1);
                &.cancel{
